.Toolbar{
    width: 100%;
    height: 7%;
    background-color: #333333;
    color: aliceblue;
    font-size: larger;
    position: fixed;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    z-index: 90;
    padding: 0 5%;
    

}
.Toolbar ul{
    list-style: none;
}

.Toolbar li{
    float: left;
    padding: 0 10px;
}

.logo{
    color: rgb(240, 67, 67);
    font-size: xx-large;
    font-style: italic;
    font-weight: bolder;
    padding-top: 12px;
} 
