.Registraion {
    margin: auto;
    box-sizing: border-box;
    background-color: rgb(233, 154, 154);
}

.RegisterBox {
    box-sizing: border-box;
    padding: 20px;
    margin: auto;
    color: bisque;

} 

.RegisterBox h2 {
    padding: 20px;
    text-align: center;
    font-weight: bolder;
    color: brown;
    font-style: italic;
}

.RegisterBox input {
    padding: 8px;
    color: lightcoral;
} 

.RegisterBox button {
    padding: 10px;
    background-color: brown;
    color: black;
    font-weight: bolder;
}